import React from "react";
import "./styles.css";
const ListItems = ({ items, GetIT }) => {
  console.log("items", items);
  const itemHandler = item => {
    GetIT(items);
  };
  return (
    <ul className="collection">
      {items.map(item => (
        <li
          key={item}
          className="collection-item"
          onClick={itemHandler(item._id)}>
          {item.driver_id}
        </li>
      ))}
    </ul>
  );
};

export default ListItems;
